import { ICompany } from "entities/company/types";
import { api } from "shared/config/api";

export const API_COMPANIES = {
  async getAll() {
    const { data: response } = await api.get<ICompany[]>("/companies");
    return response;
  },
  async add(data: ICompany) {
    const { data: response } = await api.post<ICompany>("/companies", data);
    return response;
  },
};
