import create from "zustand";
import moment from "moment";

import { ICompany, ICompanyFull } from "entities/company/types";
import { IMember } from "entities/member/types";
import { IMeeting, IQuestion } from "entities/meeting/types";
import { API_COMPANIES } from "pages/companies/api-companies";
import { API_COMPANY } from "pages/companyDetails/api-company";
import { API_MEETING } from "pages/meetingDetails/api-meeting";
import { API_QUESTIONS } from "pages/meetingDetails/blocks/questionsBlock/api-questions";
import { IAnswer } from "entities/answer/types";
import { API_ANSWERS } from "pages/meetingDetails/blocks/answersBlock/api-answers";
import { dateWithTimeGetFormat } from "features/dates";
import { API_CATALOGS } from "shared/api/api-catalogs";

interface CompanyState {
  companies: ICompany[];
  getCompanies: () => void;
  addCompany: (newCompany: ICompany) => void;
  company: ICompanyFull;
  getCompany: (id: string) => void;
  addMember: (data: IMember) => void;
  meeting: IMeeting;
  getMeeting: (id: string) => void;
  addMeeting: (company_id: string, name: string) => void;
  changeMeetingInfo: (meetingInfo: any) => void;
  addQuesion: (data: IQuestion) => void;
  addAnswer: (data: IAnswer[]) => void;
  catalogs: any;
  getGatalog: (type: string) => void;
}

export const useStore = create<CompanyState>()((set) => ({
  companies: [],
  company: {
    id: "",
    requisites: { name: "", inn: "", kpp: "", address: "" },
    members: [],
    meetings: [],
  },
  getCompanies: async () => {
    const response = await API_COMPANIES.getAll();
    set({ companies: response });
  },
  addCompany: async (newCompany) => {
    const response = await API_COMPANIES.add(newCompany);
    set((state) => ({ companies: [...state.companies, response] }));
  },
  getCompany: async (id: string) => {
    const response = await API_COMPANY.get(id);
    set({ company: response });
  },
  addMember: async (data: IMember) => {
    const response = await API_COMPANY.addMember(data);
    set((state) => ({
      company: { ...state.company, members: [...state.company.members, data] },
    }));
    // set((state) => ({ company: {...state.company, members: [...state.company?.members, response]} }));
  },
  meeting: {
    id: "",
    name: "",
    info: { name: "" },
    // typeOf–°ause: '',
    questions: [],
    answers: [],
  },
  getMeeting: async (id: string) => {
    const response = await API_MEETING.get(id);
    set({
      meeting: {
        ...response,
        info: {
          ...response.info,
          date_of_meeting:
            response.info.date_of_meeting &&
            moment(response.info.date_of_meeting, dateWithTimeGetFormat),
          date_of_registration:
            response.info.date_of_registration &&
            moment(response.info.date_of_registration, dateWithTimeGetFormat),
        },
      },
    });
    // set({ meeting: response});
  },
  addMeeting: async (company_id: string, name: string) => {
    const response = await API_MEETING.add(company_id, name);
    set((state) => ({
      company: {
        ...state.company,
        meetings: [...state.company.meetings, response],
      },
    }));
  },
  changeMeetingInfo: async (meetingInfo: any) => {
    const response = await API_MEETING.changeInfo(meetingInfo);
    set((state) => ({
      meeting: {
        ...state.meeting,
        info: { ...state.meeting.info, meetingInfo },
      },
    }));
  },
  addQuesion: async (data: IQuestion) => {
    const response = await API_QUESTIONS.add(data);
    set((state) => ({
      meeting: {
        ...state.meeting,
        questions: [...state.meeting.questions, response],
      },
    }));
  },
  addAnswer: async (data: IAnswer[]) => {
    const response = await API_ANSWERS.add(data);
    set((state) => ({
      meeting: {
        ...state.meeting,
        answers: [...state.meeting.answers, ...response],
      },
    }));
  },
  catalogs: {},
  getGatalog: async (type: string) => {
    const response = await API_CATALOGS.get(type);
    set((state) => ({
      catalogs: {
        ...state.catalogs,
        [type]: response,
      },
    }));
  },
}));
