import { ICompany, ICompanyFull } from "entities/company/types";
import { IMeeting } from "entities/meeting/types";
import { IMember } from "entities/member/types";
import { api } from "shared/config/api";

export const API_MEETING = {
  async get(id: string) {
    const { data: response } = await api.get<IMeeting>("/meetings/" + id);
    return response;
  },
  async add(company_id: string, name: string) {
    const { data: response } = await api.post<IMeeting>("/meetings/", {
      company_id,
      name,
    });
    return response;
  },
  async changeInfo(data: IMeeting) {
    const { data: response } = await api.put<any>("/meetings/", data);
    return response;
  },
  //   async addMember(data: IMember) {
  //     const { data: response } = await api.post<ICompany>("/members/", data);
  //     return response;
  //   },
};
