import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, Modal, Select } from "antd";
import { ICompany } from "entities/company/types";
import { IQuestion } from "entities/meeting/types";
import { IMember, MemberTypeEnum } from "entities/member/types";
import { useCallback } from "react";
import { IAnswer, RESULT_TYPES } from "./types";

const { Option } = Select;

export default NiceModal.create(
  ({
    company,
    questions,
    members,
    answer,
    answers
  }: {
    company: ICompany;
    questions: IQuestion[];
    members: IMember[];
    answer: IAnswer;
    answers: any[];
  }) => {
    const modal = useModal();
    const [form] = Form.useForm();

    console.log('answer', answer)
    console.log('answers', answers)

    const answeredMembers = answers.map(answer => answer.member_id)
    const currentMembers = members.filter(member => member.type.includes(MemberTypeEnum.owner) && !answeredMembers.includes(member.id))

    const handleSubmit = useCallback(() => {
      form.validateFields().then(() => {
        const values = { ...form.getFieldsValue() };
        const answers = Object.keys(values).filter(item => item !== 'member_id').map(filterItem => ({question_id: +filterItem, member_id: values.member_id, result: values[filterItem]}))
        modal.resolve(answers);
        modal.hide();
      });
    }, [
      modal,
      // user,
      form,
    ]);
    return (
      <Modal
        {...antdModal(modal)}
        title="–î–æ–±–∞–≤–∏—Ç—å –æ—Ç–≤–µ—Ç"
        //   okText={user ? "Update" : "Create"}
        okText="–°–æ—Ö—Ä–∞–Ω–∏—Ç—å"
        cancelText="–û—Ç–º–µ–Ω–∞"
        onOk={handleSubmit}
      >
        <Form form={form} layout="vertical" initialValues={answer}>
          <Form.Item
            name="member_id"
            label="–£—á–∞—Å—Ç–Ω–∏–∫"
            rules={[
              {
                required: true,
                message: "Please input the title of collection!",
              },
            ]}
          >
            <Select
              allowClear
              style={{ width: "100%" }}
              // defaultValue={["a10", "c12"]}
              // onChange={handleChange}
            >
              {currentMembers.map((member) => (
                <Option
                  key={member.id}
                  value={member.id}
                >{`${member.sirname} ${member.firstname} ${member.lastname}`}</Option>
              ))}
            </Select>
          </Form.Item>
          {questions.map((question) => {
            return (
              <Form.Item
                key={question.index}
                name={question.id}
                label={`–í–æ–ø—Ä–æ—Å ‚Ññ${question.index}. ${question.solution}`}
                rules={[
                  {
                    required: true,
                    message: "Please input the title of collection!",
                  },
                ]}
              >
                <Select
                  allowClear
                  style={{ width: "100%" }}
                  // onChange={handleChange}
                >
                  {Object.keys(RESULT_TYPES).map((item, index) => (
                    <Option key={index} value={item}>{RESULT_TYPES[item]}</Option>
                  ))}
                </Select>
              </Form.Item>
            );
          })}
        </Form>
      </Modal>
    );
  }
);
