import { Button, Card } from "antd";
import { FC, useCallback, useState } from "react";
import AnswerForm from "entities/answer/form";
import { useModal } from "@ebay/nice-modal-react";
import { IMeeting, IQuestion } from "entities/meeting/types";
import { useStore } from "entities/store";
import { IMember } from "entities/member/types";
import { IAnswer, RESULT_TYPES } from "entities/answer/types";
import styles from "./styles.module.scss";


interface IProps {
  questions: IQuestion[];
  meetingId: string;
  members: IMember[];
  meeting: IMeeting;
}

const groupAnswersByMembers = (answers: IAnswer[], property: keyof IAnswer) => {
  return answers.reduce((acc: {[key: string]: any[]}, obj) => {
    // const key = obj['member_id'];
    const key = obj[property];
    if (!acc[key]) {
       acc[key as keyof typeof acc] = [];
    }
    acc[key as keyof typeof acc].push(obj);
    return acc;
 }, {}); 
}

export const AnswersBlock: FC<IProps> = ({ meeting, meetingId, members, questions }) => {
  const userModal = useModal(AnswerForm);
  const addAnswer = useStore(state => state.addAnswer)


  const handleNewItem = useCallback(() => {
    userModal.show({ questions, members, answers: meeting.answers }).then((newItem: any) => {
      addAnswer(newItem)
      // setItems([newItem, ...items]);
      // addQuestion(newItem, meetingId)
    });
  }, [userModal, meeting.questions]);

  const handleChangeItem = useCallback((answer: any) => {
    userModal.show({ answer }).then((newMember: any) => {
      // addMember({ ...newMember, company_id });
    });
  }, [userModal, questions]);

  const currentAnswersByMember = groupAnswersByMembers(meeting.answers, 'member_id')

  return (
    <div>
      <Button type="primary" onClick={handleNewItem}>
        + –î–æ–±–∞–≤–∏—Ç—å –æ—Ç–≤–µ—Ç
      </Button>
      <div className="cards">
        {
          Object.keys(currentAnswersByMember).map((item) => {
              console.log("üöÄ ~ file: index.tsx ~ line 60 ~ Object.keys ~ item", item)
              const member = members.find(member => member.id === +item)
              return <Card hoverable title={`${member?.sirname} ${member?.firstname} ${member?.lastname}, ${member?.part}%`} key={member?.id} onClick={() => handleChangeItem(item)}>
                {
                  currentAnswersByMember[item].map(answer => {
                    const question = meeting.questions.find(question => question.id === answer.question_id)
                    return <div key={answer.id}>
                      {question?.solution} - <span>{RESULT_TYPES[answer.result]}</span>
                    </div>
                  })
                }
              </Card>            
          })
        }
      </div>
    </div>
  );
};
