import NiceModal, { useModal, antdModal } from "@ebay/nice-modal-react";
import { Form, Input, Modal, Select, Upload } from "antd";
import { useStore } from "entities/store";
import { useCallback } from "react";
import { IQuestion } from "./types";

const { TextArea } = Input;
const { Dragger } = Upload;
const { Option } = Select;

export default NiceModal.create(({ question }: { question: IQuestion }) => {
  const modal = useModal();
  const [form] = Form.useForm();


  const { question_type } = useStore((state) => state.catalogs);

  const handleSubmit = useCallback(() => {
    form.validateFields().then(() => {
      const newQuestion = { ...form.getFieldsValue() };
      modal.resolve(newQuestion);
      modal.hide();
    });
  }, [
    modal,
    // user,
    form,
  ]);

  const onTypeQuestionChange = (value: any) => {
    const nameOfQuestionType = question_type.find((question: any) => question.id === value)
    form.setFieldsValue({ name: nameOfQuestionType.name });
  }



  return (
    <Modal
      {...antdModal(modal)}
      title="–í–æ–ø—Ä–æ—Å –ø–æ–≤–µ—Å—Ç–∫–∏"
      //   okText={user ? "Update" : "Create"}
      onOk={handleSubmit}
    >
      <Form form={form} layout="vertical" initialValues={question}>
        <Form.Item
          name="question_type_id"
          label="–¢–∏–ø –≤–æ–ø—Ä–æ—Å–∞"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <Select allowClear style={{ width: "100%" }} onChange={onTypeQuestionChange}>
            {question_type.map((item: any) => (
              <Option key={item.id} value={item.id}>{item.name}</Option>
            ))}
          </Select>
        </Form.Item>
        <Form.Item
          name="name"
          label="–í–æ–ø—Ä–æ—Å"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <TextArea />
        </Form.Item>
        <Form.Item
          name="solution"
          label="–ü—Ä–µ–¥–ª–æ–∂–µ–Ω–Ω–æ–µ —Ä–µ—à–µ–Ω–∏–µ"
          rules={[
            {
              required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <TextArea />
        </Form.Item>
        <Form.Item
          name="description"
          label="–ü–æ—è—Å–Ω–µ–Ω–∏—è"
          rules={[
            {
              // required: true,
              message: "Please input the title of collection!",
            },
          ]}
        >
          <TextArea rows={8} />
        </Form.Item>

        {/* <Form.Item
          name="files"
          label="–î–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ —Ñ–∞–π–ª—ã"

        >
          <Dragger 
          >
            <p className="ant-upload-drag-icon">
            </p>
            <p className="ant-upload-text">
              –ó–∞–≥—Ä—É–∑–∏—Ç–µ –Ω–µ–æ–±—Ö–æ–¥–∏–º—ã–µ –¥–æ–∫—É–º–µ–Ω—Ç—ã
            </p>
            <p className="ant-upload-hint">
              –ü–µ—Ä–µ–Ω–µ—Å–∏—Ç–µ –Ω—É–∂–Ω—ã–µ —Ñ–∞–π–ª—ã –∏–ª–∏ –Ω–∞–∂–º–∏—Ç–µ –Ω–∞ —ç—Ç—É –∫–Ω–æ–ø–∫—É
            </p>
          </Dragger>
        </Form.Item> */}
      </Form>
    </Modal>
  );
});
